@font-face {
  font-family: "Roboto-Light";
  src: url("../assets/fonts/roboto/Roboto-Light.ttf");
}

@font-face {
  font-family: "Roboto-Regular";
  src: url("../assets/fonts/roboto/Roboto-Regular.ttf");
}

@font-face {
  font-family: "roboto-Bold";
  src: url("../assets/fonts/roboto/Roboto-Bold.ttf");
}

@font-face {
  font-family: "Roboto-Black";
  src: url("../assets/fonts/roboto/Roboto-Black.ttf");
}

.roboto-light {
  font-family: "Roboto-Light";
}

.roboto-bold {
  font-family: "Roboto-Bold";
}

.roboto-black {
  font-family: "Roboto-Black";
}

body,
html {
  padding: 0;
  margin: 0;
  font-family: "Roboto-Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="flex-"],
[class*=" flex-"] {
  display: flex;
  justify-content: center;
  height: 100%;
}

.flex-column {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.app-wrapper {
  display: grid;
  grid-template-areas:
    "header"
    "main";
  grid-template-rows: 40px calc(100vh - 40px);
}

.header {
  z-index: 3;
  grid-area: header;
  box-shadow: 0 1px 3px 0 rgba(4, 48, 90, 0.432);
  -webkit-box-shadow: 0 1px 3px 0 rgba(4, 48, 90, 0.432);
  -moz-box-shadow: 0 1px 3px 0 rgba(4, 48, 90, 0.432);
  background-color: #3498db;
}

.main {
  grid-area: main;
}

.header-wrapper {
  text-align: center;
  color: #fff;
  padding: 0 10px;
  align-items: center;
  justify-items: baseline;
}

.app-title-wrapper {
  flex: 3;
}

.app-title {
  font-family: "Roboto-Light";
}

.hamburger-wrapper {
  cursor: pointer;
  height: 30px;
  padding: 5px;
}

.hamburger {
  background-color: #fff;
  width: 30px;
  height: 5px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  left: 0;
  margin-top: 12px;
  position: relative;
  transition: all 0.3s ease-in-out;
}

.hamburger::before,
.hamburger::after {
  content: "";
  background-color: #fff;
  width: 30px;
  height: 5px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  position: absolute;
  left: 0;
  transition: all 0.3s ease-in-out;
}

.hamburger::before {
  transform: translateY(-9px);
}

.hamburger::after {
  transform: translateY(9px);
}

.hamburger.active {
  background: transparent;
  transform: translateX(-40px);
}

.hamburger.active::before {
  transform: rotate(45deg) translate(29px, -29px);
}

.hamburger.active::after {
  transform: rotate(-45deg) translate(29px, 29px);
}

.search-icon-wrapper {
  background-color: rgb(245, 245, 245);
  padding: 3px 4px;
  cursor: pointer;
  display: none;
  outline: none;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
}

.sidebar-wrapper {
  flex-basis: 180px;
  z-index: 3;
  border-right: 1px #e6e6e6 solid;
  position: relative;
}

.sidebar-wrapper::after {
  content: "";
  position: absolute;
  top: -4px;
  margin-left: 25px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #fff;
}

.book-lists {
  height: calc(100vh - 42px);
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  margin-top: 2px;
}

.book-lists li {
  list-style: none;
  margin: 4px 0;
  position: relative;
}

.book-lists button {
  outline: 0;
  text-align: left;
  font-size: 0.9em;
  cursor: pointer;
  width: 100%;
  padding: 7px;
  border: 0;
  text-indent: 10px;
  color: #777;
  background-color: transparent;
}

.book-lists button:hover {
  background-color: #eee;
  color: #073655;
}

.book-lists button.active {
  font-family: "Roboto-Bold";
  background-color: #eee;
  color: #073655;
}

.book-lists button.active::before {
  content: "";
  position: absolute;
  left: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid rgb(0, 0, 0, 0.8);
}

.chapters-count {
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  background-color: #eee;
  color: #073655;
  font-size: 0.7em;
  padding: 3px;
  font-family: "Roboto-Bold";
  margin-left: 5px;
  border: 1px solid rgb(0, 0, 0, 0.2);
}

.searchbar {
  height: 50px;
  background-color: #f9f9f9;
  border-bottom: 1px solid #d8d8d8;
  align-items: center;
  padding: 0 10px;
}

.search-div {
  flex: 2;
  height: auto;
}

.search-wrapper {
  flex: 1;
}

.search {
  width: 100px;
  text-align: center;
  padding: 7px 25px;
  border: 1px #dcdcdc solid;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  outline: none;
  font-style: italic;
}

.clear-search {
  width: 12px;
  height: 12px;
  position: absolute;
  margin-top: 3px;
  margin-left: -27px;
  cursor: pointer;
  outline: none;
  padding: 7px;
}

.search:focus {
  font-style: normal;
}

.verse-wrapper {
  flex: 1;
}

.verse-header {
  flex: 1;
  color: #073655;
}

.verse-header span {
  margin: 0 2px;
}

.select-chapter {
  width: 150px;
  cursor: pointer;
  padding: 6px 15px;
  font-size: 0.9em;
  outline: none;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  margin-left: 3px;
  border: 1px #dcdcdc solid;
  background-color: #fff;
}

.btn-wrapper button {
  width: 150px;
  margin: 0 2px;
  border: 1px #21618c solid;
  padding: 7px 15px;
  cursor: pointer;
  background-color: #21618c;
  color: #fff;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  font-size: 0.8em;
  outline: none;
}

.btn-wrapper button:hover {
  background-color: #317aac;
}

.btn-wrapper button:last-child {
  float: right;
}

.verse-details {
  height: calc(100vh - 131px);
  overflow-y: auto;
  padding: 20px;
}

.verse-number {
  cursor: pointer;
  padding: 2px 5px;
  background-color: #eee;
  color: #0a3d5f;
  font-size: 1.2em;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  margin: 0 3px;
  font-family: "Roboto-Bold";
}

.verse-text {
  font-family: "Roboto-Light";
  font-size: 1.3em;
  line-height: 2;
}

.loading {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto-Bold";
}

.reload-wrapper {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 7px;
  background-color: #f3f3f3;
  border-top: 1px solid #e4e4e4;
}

.reload {
  width: 80px;
  margin: auto;
  padding: 5px;
  border-radius: 3px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #dcdcdc;
  cursor: pointer;
}

.reload-wrapper span {
  font-family: "Roboto-Light";
  font-size: 0.8em;
}

@media (max-width: 1024px) {
  .main {
    display: inline-block;
  }

  .sidebar-wrapper {
    position: absolute;
    background-color: #fff;
    box-shadow: 0 1px 9px 0 rgb(0, 0, 0, 0.2);
    -webkit-box-shadow: 0 1px 9px 0 rgb(0, 0, 0, 0.2);
    -moz-box-shadow: 0 1px 9px 0 rgb(0, 0, 0, 0.2);
  }
}

@media (max-width: 768px) {
  .search-icon-wrapper {
    display: block;
  }

  .searchbar {
    flex-direction: column;
    height: auto;
    position: relative;
  }

  .verse-header {
    padding: 10px 0;
  }

  .search-div {
    position: absolute;
    flex-direction: column;
    padding: 10px 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 75px;
    z-index: 4;
    background-color: #fff;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.4);
  }

  .search-div::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -4px;
    margin-right: 17px;
    border-bottom: 5px solid #fff;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
  }

  .search-wrapper,
  .btn-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .verse-details {
    padding: 0 7px;
    height: calc(100vh - 90px);
  }

  .clear-search {
    position: relative;
  }
}

@media (max-width: 425px) {
  .verse-text {
    font-size: 1.1em;
    line-height: 1.8;
  }

  .verse-number {
    font-size: 1.2em;
  }
}
